import { locationAssign } from '../../utils/helper';

const onExport = (baseUrl, setExportEnabled) => values => {
  setExportEnabled(false);
  const {
    objectType,
    object,
    format,
    compression,
    skipSharing
  } = values; // generate URL and redirect

  const apiBaseUrl = "".concat(baseUrl, "/api/");
  const endpoint = "".concat(objectType, "/").concat(object, "/metadata");
  const endpointExtension = compression ? "".concat(format, ".").concat(compression) : format;
  const downloadUrlParams = "skipSharing=".concat(skipSharing, "&download=true");
  const url = "".concat(apiBaseUrl).concat(endpoint, ".").concat(endpointExtension, "?").concat(downloadUrlParams);
  locationAssign(url, setExportEnabled); // log for debugging purposes

  console.log('metadata-dependency-export:', {
    url,
    params: downloadUrlParams
  });
};

export { onExport };