import { DATE_BEFORE_VALIDATOR, DATE_AFTER_VALIDATOR } from '../../components/DatePicker/DatePickerField';
import { ALL_VALUE } from '../../hooks/useProgramStages';
import { jsDateToISO8601, locationAssign, pathToId } from '../../utils/helper';

const onExport = (baseUrl, setExportEnabled) => values => {
  setExportEnabled(false);
  const {
    selectedOrgUnits,
    selectedPrograms,
    programStage,
    format,
    compression,
    startDate,
    endDate,
    includeDeleted,
    dataElementIdScheme,
    orgUnitIdScheme,
    idScheme,
    inclusion
  } = values; // generate URL and redirect

  const apiBaseUrl = "".concat(baseUrl, "/api/");
  const endpoint = "events";
  const endpointExtension = compression ? "".concat(format, ".").concat(compression) : format;
  const filename = "".concat(endpoint, ".").concat(endpointExtension);
  const downloadUrlParams = ['links=false', 'skipPaging=true', "orgUnit=".concat(pathToId(selectedOrgUnits[0])), "program=".concat(selectedPrograms), "includeDeleted=".concat(includeDeleted), "dataElementIdScheme=".concat(dataElementIdScheme), "orgUnitIdScheme=".concat(orgUnitIdScheme), "idScheme=".concat(idScheme), "attachment=".concat(filename), "startDate=".concat(jsDateToISO8601(startDate)), "endDate=".concat(jsDateToISO8601(endDate)), "ouMode=".concat(inclusion), "format=".concat(format), programStage != ALL_VALUE ? "programStage=".concat(programStage) : ''].filter(s => s != '').join('&');
  const url = "".concat(apiBaseUrl).concat(endpoint, ".").concat(endpointExtension, "?").concat(downloadUrlParams);
  locationAssign(url, setExportEnabled); // log for debugging purposes

  console.log('event-export:', {
    url,
    params: downloadUrlParams
  });
};

const validate = values => ({
  startDate: DATE_BEFORE_VALIDATOR(values.startDate, values.endDate),
  endDate: DATE_AFTER_VALIDATOR(values.endDate, values.startDate)
});

export { onExport, validate };