import { FORM_ERROR, jobStartedMessage } from '../../utils/final-form';
import { uploadFile } from '../../utils/helper';
const isAsync = true;

const onImport = ({
  baseUrl,
  setProgress,
  addTask,
  setShowFullSummaryTask
}) => async values => {
  const {
    dryRun,
    files,
    format,
    dataElementIdScheme,
    orgUnitIdScheme,
    eventIdScheme,
    idScheme
  } = values; // send xhr

  const apiBaseUrl = "".concat(baseUrl, "/api/");
  const endpoint = 'events.json';
  const params = ['skipFirst=true', "async=".concat(isAsync), "dryRun=".concat(dryRun), "dataElementIdScheme=".concat(dataElementIdScheme), "orgUnitIdScheme=".concat(orgUnitIdScheme), "eventIdScheme=".concat(eventIdScheme), "idScheme=".concat(idScheme), "payloadFormat=".concat(format)].join('&');
  const url = "".concat(apiBaseUrl).concat(endpoint, "?").concat(params);

  try {
    await uploadFile({
      url,
      file: files[0],
      format: format,
      type: 'EVENT_IMPORT',
      isAsync: isAsync,
      setProgress,
      addEntry: (id, entry) => addTask('event', id, { ...entry,
        jobDetails: values
      })
    });
    return jobStartedMessage;
  } catch (e) {
    const errors = [e];
    return {
      [FORM_ERROR]: errors
    };
  } finally {
    setShowFullSummaryTask(true);
  }
};

export { onImport };