import i18n from '@dhis2/d2-i18n';
import { FORM_ERROR, jobStartedMessage } from '../../utils/final-form';
import { uploadFile } from '../../utils/helper';

const onImport = ({
  baseUrl,
  setProgress,
  addTask,
  setShowFullSummaryTask
}) => async values => {
  const {
    dryRun,
    files,
    format,
    identifier,
    importReportMode,
    importStrategy,
    atomicMode,
    mergeMode,
    flushMode,
    skipSharing,
    skipValidation,
    inclusionStrategy,
    isAsync,
    firstRowIsHeader,
    classKey
  } = values; // send xhr

  const apiBaseUrl = "".concat(baseUrl, "/api/");
  const endpoint = 'metadata.json';
  const params = ["importMode=".concat(dryRun ? 'VALIDATE' : 'COMMIT'), "identifier=".concat(identifier), "importReportMode=".concat(importReportMode), "importStrategy=".concat(importStrategy), "atomicMode=".concat(atomicMode), "mergeMode=".concat(mergeMode), "flushMode=".concat(flushMode), "skipSharing=".concat(skipSharing), "skipValidation=".concat(skipValidation), "inclusionStrategy=".concat(inclusionStrategy), "async=".concat(isAsync), "format=".concat(format), format == 'csv' ? "firstRowIsHeader=".concat(firstRowIsHeader, "&classKey=").concat(classKey) : ''].filter(s => s != '').join('&');
  const url = "".concat(apiBaseUrl).concat(endpoint, "?").concat(params);

  try {
    await uploadFile({
      url,
      file: files[0],
      format: format,
      type: 'METADATA_IMPORT',
      isAsync: isAsync,
      setProgress,
      addEntry: (id, entry) => addTask('metadata', id, { ...entry,
        jobDetails: values
      })
    });
    return jobStartedMessage;
  } catch (e) {
    const formErrors = validate(values);
    const allErrors = {
      [FORM_ERROR]: [e],
      ...formErrors
    };
    return allErrors;
  } finally {
    setShowFullSummaryTask(true);
  }
};

const validate = values => {
  const classKeyValidator = (format, classKey) => format == 'csv' && !classKey ? i18n.t('A class key must be selected') : undefined;

  return {
    classKey: classKeyValidator(values.format, values.classKey)
  };
};

export { onImport, validate };