  const params = ["importMode=".concat(dryRun ? 'VALIDATE' : 'COMMIT'), "identifier=".concat(identifier), "importReportMode=".concat(importReportMode), "preheatMode=".concat(preheatMode), "strategy=".concat(strategy), "atomicMode=".concat(atomicMode), "mergeMode=".concat(mergeMode), "flushMode=".concat(flushMode), "skipSharing=".concat(skipSharing), "skipValidation=".concat(skipValidation), "inclusionStrategy=".concat(inclusionStrategy), "async=".concat(isAsync), "format=".concat(format), "dataElementIdScheme=".concat(dataElementIdScheme), "orgUnitIdScheme=".concat(orgUnitIdScheme), "eventIdScheme=".concat(eventIdScheme), "idScheme=".concat(idScheme)].filter(s => s != '').join('&');
  const url = "".concat(apiBaseUrl).concat(endpoint, "?").concat(params);

